import React, { useEffect, useRef } from "react";
import './style.scss';

export const Map: React.FC = () => {
  const container = useRef<HTMLDivElement | null>(null); //   

  useEffect(() => {
    const loadMap = () => {
      if (container.current) {
        const position = new (window as any).kakao.maps.LatLng(37.53916746934834, 127.06966568023567);
        const options = {
          center: position, //   
          level: 3, //   
        };
        
        //  
        const map = new (window as any).kakao.maps.Map(container.current, options);

        //  
        const marker = new (window as any).kakao.maps.Marker({
          position: position, //  
          map: map, //   
        });
      }
    };

    const script = document.createElement("script");
    script.src = "https://dapi.kakao.com/v2/maps/sdk.js?appkey=e969b8fecceb75ea37f96100d4047054&autoload=false"; // API  
    script.onload = () => {
      (window as any).kakao.maps.load(loadMap); // kakao.maps.load 
    };
    document.body.appendChild(script);

    return () => {
      // Cleanup
      document.body.removeChild(script);
    };
  }, []);

  return (
    <>
        <div className="map" ref={container}></div>
    </>
  );
};
